@import url('https://fonts.googleapis.com/css?family=Alata');

.App {
  display: flex;
  flex-direction: column;
  background-color: white;
}

.Header {
  width: 100%;
  height: 10vh;
  background-color: white;
  padding-left: 2vw;
}

.Main {
  width: 100%;
  height: 90vh;
  background-color: red;
  display: flex;
  flex-direction: row;
}

.Main-side {
  width: 30vw;
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
}

.Main-content {
  width: 70vw;
  height: 100%;
  background-color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}


.Card-container {
  width: 20vw;
  height: 31.5vw;
  border-style: solid;
  border-radius: 7px;
  border-color: black;
  border-width: 3px;
  display: flex;
  box-shadow: 6px 6px 6px black;
  background-color: white;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2vw;
  cursor: pointer;
  font-family: Alata;
}

.Card-img {
  width: 18vw;
  height: 18vw;
  border-radius: 7px;
  margin-top: 1vw;
}

.Card-description {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 14vw;
  height: 14vw;
  border-radius: 7px;
  margin-top: 1vw;
  font-size: 1.5vw;
  font-weight: 800;
  padding: 2vw;
}

.Card-text-container {
  width: 18vw;
  height: 8vw;
  margin-top: 1vw;
  background-color: white;
  display: flex;
  flex-direction: column;
}

.Card-name-price-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}

.Card-price-div {
  display: flex;
  align-items: center;
  justify-content: center;
  border-style: solid;
  border-color: black;
  border-width: 1;
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
  height: 2vw;
  font-weight: 700;
  background-color: lightgreen;
  margin-top: 0;
  margin-bottom: 0;
}

h2 {
  font-size: 1.7rem;
  margin: 0;
  padding: 0;
}

.Card-weight {
  margin: 0;
}

.Card-description-label {
  margin: 0;
  font-weight: 600;
  display: inline;
}

.Card-heart-btn {
  width: 17.5vw;
  height: 5vw;
  border-style: solid;
  border-radius: 5px;
  border-color: black;
  border-width: 2;
  margin-top: 0;
  background-color: #cccccc;
  display: flex;
  justify-content: center;
  text-align: center;
  cursor: pointer;
}

.Card-heart-btn-txt {
  color: black;
  font-weight: 700;
  user-select: none;
}

.Panel-container {
  width: 20vw;
  height: 26vw;
  border-style: solid;
  border-radius: 7px;
  border-color: black;
  border-width: 3px;
  display: flex;
  flex-direction: column;
  box-shadow: 6px 6px 6px black;
  background-color: white;
  margin-top: 5vw;
}

.Panel-title {
  color: black;
  font-weight: 700;
  align-self: center;
  font-size: 28px;
}

.Panel-line {
  width: 100%;
  border-color: black;
  border-style: solid;
  border-width: .1;
  opacity: .5;
}

.Panel-checkbox-row {
  display: flex;
  flex-direction: row;
  width: 80%;
  align-self: center;
  margin-top: .5vw;
}

.Panel-label {
  font-weight: 700;
  color: black;
  font-size: 1.2rem;
}

.Panel-cost {
  margin: 0;
  font-weight: 700;
}

.Reset-btn {
  border-radius: 8px;
  background-color: lightblue;
  border-style: solid;
  border-width: 2px;
  border-color: black;
  cursor: pointer;
  width: 7vw;
  height: 2.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  align-self: center;
  margin-top: 1.7vw;
}

.ItemPage-container {
  display: flex;
  flex-direction: row;
  width: 100vw;
  height: 100vh;
}

.Pet-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70vw;
  height: 40vw;
  border-radius: 1vw;
  border-style: solid;
  border-color: black;
  border-width: 3px;
  outline: 0;
  background-color: white;
}

.Pet-modal-x {
  position: absolute;
  top: .8vw;
  right: 1.6vw;
  font-size: 1.7rem;
  font-weight: 800;
  cursor: pointer;

}

.Pet-modal-x:hover {
  color: #777777;
}

.Pet-modal-img {
  width: 19vw;
  height: 19vw;
  margin-top: 3vw;
  border-style: solid;
  border-color: black;
  align-self: center;
}

.Pet-modal-left {
  display: flex;
  flex-direction: column;
  width: 40%;
  height: 100%;
}

.Pet-modal-right {
  display: flex;
  flex-direction: column;
  width: 60%;
  height: 30vw;
  margin-top: 5vw;
  justify-content: space-between;
}

.Pet-modal-name {
  display: flex;
  align-self: center;
  font-family: Alata;
  font-size: 1.9rem;
  font-weight: 700;
  margin-top: .5vw;
}

.Pet-modal-description {
  display: flex;
  width: 16vw;
  align-self: center;
  font-family: Alata;
  font-size: 1.15rem;
  font-weight: 700;
  padding: 1.7vw;
  border-radius: 1vw;
  margin-top: 2vw;
  border-style: solid;
  border-color: black;
  border-width: 2px;
}

.Modal-heart-btn {
  width: 17.5vw;
  height: 4.5vw;
  border-style: solid;
  border-radius: 5px;
  border-color: black;
  border-width: 2;
  margin-top: 0;
  background-color: #cccccc;
  display: flex;
  justify-content: center;
  text-align: center;
  cursor: pointer;
  align-self: center;
}

.Modal-heart-btn-txt {
  color: black;
  font-weight: 700;
  user-select: none;
  font-size: 1.3rem;
}
